
.books-page {
    padding: 20px;
  }
  
  .book-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .book-item {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 15px;
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .book-item:hover {
    transform: translateY(-5px);
  }
  
  .book-item img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
  }
  
  .book-item h3 {
    margin: 10px 0;
  }
  
  .book-item p {
    color: #666;
  }
  
  .book-item a {
    display: inline-block;
    margin-top: 10px;
    color: #007bff;
  }
  