
.book-detail-page {
    padding: 20px;
  }
  
  .book-detail-container {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
  }
  
  .book-detail-image {
    max-width: 300px;
    height: auto;
    border-radius: 8px;
  }
  
  .book-detail-info {
    flex: 1;
  }
  
  .book-detail-info h2 {
    margin-top: 0;
  }
  
  .book-detail-info p {
    margin: 10px 0;
  }
  
  .buy-button {
    display: inline-block;
    background-color: #28a745;
    color: white;
    padding: 10px 15px;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease;
  }
  
  .buy-button:hover {
    background-color: #218838;
  }
  /* BookDetail.css */
.back-link {
    display: inline-block;
    font-weight: bold;
    text-decoration: none;
    margin-bottom: 20px;
    color: #007bff;
  }
  
  .back-link:hover {
    text-decoration: underline;
  }
  