
.book-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    margin: 20px auto;
    padding: 0 20px; /* Add padding to fit better on wider screens */
    max-width: 95vw; /* Adjusts to fit the viewport width */
  }
  
  .book-item {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 15px;
    text-align: center;
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .book-item:hover {
    transform: translateY(-5px);
  }
  
  .book-item img {
    max-width: 100%;
    height: auto;
    border-radius: 5px;
  }
  
  .book-item h3 {
    margin: 10px 0;
    font-size: 1.2rem;
    text-transform: uppercase;
  }
  
  .book-item p {
    font-size: 0.9rem;
    color: #555;
  }
  
  .book-item a {
    margin-top: auto;
    color: #007bff;
    font-weight: bold;
    text-decoration: none;
  }
  
  .book-item a:hover {
    text-decoration: underline;
  }
  