
.about-page {
    padding: 40px;
    text-align: center;
    background-color: white;
    border-radius: 8px;
    max-width: 800px;
    margin: 20px auto;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .about-page h2 {
    margin-bottom: 20px;
  }
  
  .about-page p {
    margin-bottom: 15px;
  }
  .back-link {
    display: inline-block;
    font-weight: bold;
    text-decoration: none;
    margin-bottom: 20px;
    color: #007bff;
  }
  
  .back-link:hover {
    text-decoration: underline;
  }